<template>
  <div class="board-list">    
    <form v-on:submit.prevent="submit" class="card-body cardbody-color p-lg-5">
            <div class="mb-3">
              <input type="email" class="form-control" id="email" v-model="email" placeholder="examble@example.com">
            </div>
            <div class="mb-3">
              <input class="form-control" id="customFile" type="file" @change="handleFileUpload"/>
            </div>
            <div class="text-center"><button type="submit" class="btn btn-color px-5 mb-5 w-100">Submit</button></div>
          </form>
  </div>
</template>

<style scoped>
.btn-color{
  background-color: #226cf6;
  color: #fff; 
}

a{
  text-decoration: none;
}
</style>

<script>
export default {
  data() {
    return {
      email: "example@example.com",
      file: "",
    }
  },
  methods: {
    submit() {
      var data = new FormData();
      data.append('file', this.file);
      fetch('/echo/json/', {
        method: 'POST',
        body: data
      });
    },
    handleFileUpload(event) {
      this.file = event.target.files[0];
    }
  }
}
</script>
